var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{attrs:{"id":"reviews"}},[_vm._m(0),_c('v-spacer',{attrs:{"size":"100px"}})],1)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('article',[_c('div',{staticClass:"bubble"},[_vm._v("I have had the pleasure of working with Kim for the last year and a half. She is a perfectionist that will only deliver her best every time. As an SEO Content Manager I have worked closely with Kim on several projects. From implementing video on the site to optimizing Mobile to be responsive she is always there with efficient, streamlined solutions. Thinking out of the box is her specialty. I'm grateful to have worked with her at ToysRUs as Kim would be a tremendous asset to any team! "),_c('small',[_vm._v("- Toni Balaguer, SEO & Content Manager")])]),_c('div',{staticClass:"bubble"},[_vm._v("This is a killer website! I love it. Great job! "),_c('small',[_vm._v("- Gail Doscher, client")])]),_c('div',{staticClass:"bubble"},[_vm._v("Working with Kim made my time at New York Public Radio infinitely better! From introducing us to Vue.js so we could modernize our stack, taking the time to answer any technical questions or give advice on how to best write computed methods, she was always available without judgement. Kim also helped me a lot with building out my outside of work Vue projects when she didn't have to. Besides being an expert developer, she was also just a really fun and amazing person to work with and the team is better with her on her it. "),_c('small',[_vm._v("- Seori Sachs, Developer")])]),_c('div',{staticClass:"bubble"},[_vm._v("Thanks so much for your help with this. As always -- YOU'RE AWESOME!!! "),_c('small',[_vm._v("- Arlene Glasco, client")])]),_c('div',{staticClass:"bubble"},[_vm._v("Kim is an absolute pleasure to work for. She is always on top of every project that everyone is working on, and I know if I ask her a question I’ll get an answer. She is a great project manager not only in that she makes sure every project gets done as efficiently and as well as possible, but also that the people doing the projects are working on things that they enjoy doing! She's always pushing us to do our best and helping us move forward in our careers and in our skill levels. Kim is a great boss - probably my favorite boss I've ever had :) "),_c('small',[_vm._v("- Mariam Garibov, Developer")])]),_c('div',{staticClass:"bubble"},[_vm._v("Kim built a tool for the Great Deals Stores that is easy to use and has been an important asset to our team. Simply put, it is awesome! The Great Deals store is important for our company to showcase our best digital promotions for Toys”R”Us and Babies”R”Us. Creating these stores were very time consuming until this new tool. We are now able to copy all our deals into spreadsheets that can easily be loaded into the system. We can generate test links that look exactly like the deal stores, and can manipulate the dates to see time specific deals fall in and out of the store – a feature that is very necessary for tracking our deals during season. The GDS tool has been great for our team, has prevented us from waking up at 6am every morning to check the deals and has fostered trust in our team that the stores will go live every week. "),_c('small',[_vm._v("- David Falk, Manager, Digital Brand Marketing and Strategy")])]),_c('div',{staticClass:"bubble"},[_vm._v("I reported to Kim during my time at iMedia Inc as a front-end developer. Prior to Kim joining, the front end team lacked structure and process, each developer using different tools to get the job done without receiving full requirements. Within a few weeks Kim won over everyone on the team and got us on track with a consistent process and framework in Vue.js. Even as a new hire she was not afraid to break the status quo and held developers as well as project managers accountable for following the process and ensuring work was broken up into consumable pieces. She led by example and wrote initial components, made an effort to check in daily, and offered code reviews at any time. Kim made our team feel confident in our work and our ability to deliver, increasing our productivity while lowering the stress level. It was a pleasure working with Kim, she is a true leader and would be an asset on any team! "),_c('small',[_vm._v("- Mike Cammarato, Developer")])]),_c('div',{staticClass:"bubble"},[_vm._v("I recently had a had glitch with my e-mail program and quite a few messages were lost. How fortunate I am to have the On-Line Lead Manager database, otherwise all those leads would have been permanently lost. I am grateful that my web site is hosted with Kim. "),_c('small',[_vm._v("- Agi Anderson, client")])]),_c('div',{staticClass:"bubble"},[_vm._v("Thanks for everything. I look forward to referring you lots and lots of business--your work is AWESOME! "),_c('small',[_vm._v("- Carolyn MacEvoy, client")])]),_c('div',{staticClass:"bubble"},[_vm._v("I've worked closely with Kim on a daily basis on two projects so far - the WNYC.org/radio player redesign and re-platform, and on the Gothamist.com re-platform. She was the lead engineer and direct manager for at least one other team member also contributing to these efforts, and I was the project manager. "),_c('br'),_c('br'),_vm._v(" When Kim first arrived, she was faced right away with some management and collaboration challenges she needed to address. I saw her carefully gather data to address these concerns tactfully and honestly, first searching for data to back up the discussion points she was working on. I also saw her observe quietly the early retrospective meetings so she could get an accurate understanding of other peoples' current struggles and triumphs. She's been very fair, and she's still good at deferring to other opinions when needed, allowing others the space to speak to new areas of responsibility and deepen their expertise. "),_c('br'),_c('br'),_vm._v(" Kim often provides vocal support for accessibility considerations on our web platforms; most recently, she was able to ensure all pages on the Gothamist site are navigable by screen readers. She's obsessed (in a positive and necessary way!) with optimizing the page speed and load times of Gothamist.com, and recently suggested we do research with our advertising partner to asynchronously advertisements there and save on our page speed indexes. Given a goal metric, Kim is dogged about pushing for it in every way she can think of. "),_c('br'),_c('br'),_vm._v(" Kim works with Product managers frequently to break down the implementation steps needed for features Product wants, and is good at speaking to the time and effort also needed to make an idea into reality. She's also very generous with her time for more junior engineers and interns who want to contribute to the Digital team's work. Moreover, she knows when it makes more sense to take a back seat and listen during discussions of systems she's less familiar with, which is a time saver and frequently makes my job much easier. "),_c('br'),_c('br'),_vm._v(" Kim is great at follow through and leads by example for team members. I think over the course of her work replatforming Gothamist.com to the Vue front end framework, I had upwards of 5 behavior fixes and very specific, complicated modifications to Home page behavior. She was polite and patient, even when I had to come back with more feedback about something she had just thought she finished. She asked good questions to clarify what I expected, and her determination to get it right paid off! "),_c('br'),_c('br'),_vm._v(" Since her arrival at NYPR, Kim's been able to pitch in on a variety of platforms, sites, and systems. She always has a can-do attitude, which is invaluable in situations where morale is tenuous (including the global pandemic we're all still navigating.) I'm excited to see what ideas she has for our upcoming projects, and I'm looking forward to continuing to work with her as we create a long-term vision for NYPR Digital! "),_c('small',[_vm._v("- Hannah Warren, scrum master")])]),_c('div',{staticClass:"bubble"},[_vm._v("I am delighted with everything you have done. The Architectural Tours turned out great, in fact, the entire website is excellent. I am very pleased with it. "),_c('small',[_vm._v("- Jan Hexamer-Gardner, client")])]),_c('div',{staticClass:"bubble"},[_vm._v("The site is absolutely wonderful and has gone beyond my hopes. Thank you so much. With out you guys I would not be doing the business I am doing. It is worth every penny I paid for the site and some. You are so helpful ... Kim you have a great career ahead of you if you treat every customer the way you treat me. "),_c('small',[_vm._v("- Jeff Fulgham, client")])]),_c('div',{staticClass:"bubble"},[_vm._v("It didn’t take long to see that Kim is one of those leaders that makes a meaningful and long lasting impact not only on the team she leads but on any project she works on. She is very knowledgeable and resourceful but what sets her apart from anyone else I’ve worked with is her ability to lead by example, encourage and motivate her team to take any task at hand and use it to learn and improve their skill sets. The impression she leaves is that she is reliable and a valuable asset to any team or project she is involved with. "),_c('small',[_vm._v("- Andres Arias, developer")])]),_c('div',{staticClass:"bubble"},[_vm._v("WOW!!! You're the Super Best!!! Thank you so much for putting it together so quickly. If I had a winning race horse, I would name her KIM! "),_c('small',[_vm._v("- Julia Meadows, client")])]),_c('div',{staticClass:"bubble"},[_vm._v("You’re awesome – thank you for always being available!!! You are always so wonderful and helpful!!! "),_c('small',[_vm._v("- Julie & Ted Hummel, clients")])]),_c('div',{staticClass:"bubble"},[_vm._v("You rarely come across standout talent like Kim. I have worked closely with Kim during her time at iMedia as Lead Web Developer. She came to the front end team where there wasn’t any structure or process in place. With a couple of weeks, she quickly changed that around and had everyone follow a method for each project moving forward. She had helped complete projects on a timely matter and keeping the stress levels low. She also knowledgable and closely follows the new trends in the web development world. Apart from all the projects she works on, she held weekly lunch & learn to show anyone interested in learning about VueJS. The best thing about Kim is her relaxed attitude and sense of humor coming to work a pleasure. She the best manager I ever had, and anyone who works with her is lucky. "),_c('small',[_vm._v("- Luis Almonte, developer")])]),_c('div',{staticClass:"bubble"},[_vm._v("As usual you are the best!!! I'm very happy with my site, a lot of nice complements, thanks to you. "),_c('small',[_vm._v("- Lenise Lowry, client")])]),_c('div',{staticClass:"bubble"},[_vm._v("I will be delighted to give you an outstanding reference. The fact that you can maintain your patience and professionalism speaks well for you. I won’t go into details while I expound on the variety of services you offer, your consistency and your dedication to support your client needs. "),_c('small',[_vm._v("- Lucille Maddalena, client")])]),_c('div',{staticClass:"bubble"},[_vm._v("I reported to Kim when we worked at iMedia. She is a rockstar in every sense of the word. I’ve never worked with someone with a broader range of skills and abilities. Kim stays up to date with the very latest developer tools and trends; she shares her expertise with her teams and applies it to meeting and exceeding her clients’ expectations. Her confidence and her sense of humor are contagious and most welcome. "),_c('small',[_vm._v("- Tom Hackett, digital production coordinator")])]),_c('div',{staticClass:"bubble"},[_vm._v("I LOVE MY WEBSITE! You are so creative! Thank you for helping me. I'm so glad I found your company. "),_c('small',[_vm._v("- Nicole Perry, client")])]),_c('div',{staticClass:"bubble"},[_vm._v("Exuberant, supportive and motivating. Those are the words that I would use to describe Kim and her role at W2O Group, where I have reported to her for over 2 years. "),_c('br'),_c('br'),_vm._v(" I've watched Kim define her role as Senior Director of Development and give that title true meaning. I've been privileged to witness her taking the time to instruct junior members on language constructs, code quality, navigating the workplace or any of a myriad other tasks, all of which she does with patience and grace, never too busy to help someone out. She has also proven herself adept and willing to jump on any task and get 'elbow deep' into whatever project needs to get done, regardless of the time frame or effort involved. Often times, this willingness means coding herself, facilitating others or simply providing moral support. She has also proven an adept and capable manager, fighting for her team as a whole or individuals in particular, with conviction and the knowledge to defend them admirably. "),_c('br'),_c('br'),_vm._v(" Aside from her coding and managerial skills, Kim brings a cohesiveness and humanity to the office which is welcome and very much appreciated. She is always first with a smile and greeting and eager to generate camaraderie and promote team building during and after hours. "),_c('br'),_c('br'),_vm._v(" I'm more than happy to recommend Kim, for any position and would consider it a pleasure to be able to work with her, in any capacity. "),_c('small',[_vm._v("- Troy Larssen, Associate Director of Development")])]),_c('div',{staticClass:"bubble"},[_vm._v("Thank you, thank you. By the way, the site looks great and the control panel looks pretty user friendly. "),_c('small',[_vm._v("- Patty Johnson, client")])]),_c('div',{staticClass:"bubble"},[_vm._v("Kim is an exemplary employee who is extremely dependable and hard working. She is a go- getter who will always get the job done on task and on-time. "),_c('small',[_vm._v("- Katherine Jaucian, Digital Consultant")])]),_c('div',{staticClass:"bubble"},[_vm._v("Kim has established an outstanding web design company with quite a large client base because of three major principles: 1) A thorough understanding of the industry sectors she designs for. 2) The creation of dynamic database driven sites, which allow for a rich user experience. 3) Real-time client help. "),_c('small',[_vm._v("- Tim Maguire, Director of Corporate Development")])]),_c('div',{staticClass:"bubble"},[_vm._v("This site is very user friendly and easy to navigate. I am very impressed. I love the quick turnaround. "),_c('small',[_vm._v("- Rosalyn Blaylock, client")])]),_c('div',{staticClass:"bubble"},[_vm._v("It [the website] looks great, thank you so much for your help, and how quick you respond to me!!! "),_c('small',[_vm._v("- Ryan Bittinger, client")])]),_c('div',{staticClass:"bubble"},[_vm._v("Good evening, Tony. I've been meaning to take a moment to write you a letter. I'm glad to say that I've been busy and that is why I haven't sent this sooner. Kim LaRocca - - is absolutely INCREDIBLE!!! I couldn't be more pleased with the service I have received thus far. She has taken care of everything. I am amazed - there is not a time that I send her an e-mail that I do not receive an immediate, kind and informative response. Even in the evening! Does she not take time off??? Kudos - - - Also, I am very excited about my new website. I love the look and the feel of it. I didn't realize how boring the look of my other site was. That powder blue and beige... just let me say, the impact of my new site will no doubt grab more attention. I'll say it for you, \"you told me so\" - I should have switched months ago before it became an emergency situation. "),_c('small',[_vm._v("- Sidney L. Walker, client")])]),_c('div',{staticClass:"bubble"},[_vm._v("You're good, quick, and excellent! Thanks for your quick service! I've had many complements on my site. I really like this so much more than Informedia. The picture slide show on the listings is really impressive. I am getting plenty of inquiries lately. Thanks again! "),_c('small',[_vm._v("- Steve Mayo, client")])]),_c('div',{staticClass:"bubble"},[_vm._v("Having worked in the consulting and project management fields I was expecting my transition into a traditional development role to be a very challenging process. Thanks to Kim, my move into software development was anything but difficult. As Director of Development for my team at W2O, Kim not only encouraged me but empowered me to really grow and sharpen my skills as a developer. Whether it was taking time to personally review my code and suggest how it can be improved, providing lunch and learns on how to use developer tools efficiently or encouraging our team to get certifications and be our tutor for them, Kim has always been there to push us to do our best. "),_c('br'),_c('br'),_vm._v(" Yet, it's not just development that Kim has helped me grow in. Having a background in project management and business development, Kim encouraged me to really look at my strengths beyond development and find a way to use them to bring value to the team. Kim has always been open to hearing out my ideas on how to better communicate and improve processes between different teams and then work various groups to execute them. In this way, Kim asks me to look beyond just my code to improve a project and grow beyond just being a good software engineer. "),_c('br'),_c('br'),_vm._v(" Simply put, if you are looking for someone who is more than just a great developer, but a great evangelist and leader, look no further than Kim Larocca. She is the person you can count on to lead, motivate, and make sure that your team not only makes it to the finish line of any technical project but truly grow on the way there. "),_c('small',[_vm._v("- Geroge Ulloa, Developer")])]),_c('div',{staticClass:"bubble"},[_vm._v("THE WEB SITE LOOKS AWESOME! In case I haven't told you... thanks for all your hard work! "),_c('small',[_vm._v("- Trey Pierson, client")])]),_c('div',{staticClass:"bubble"},[_vm._v("Kim: Thank you for all your help on this. If all were like you life would be a lot more enjoyable. "),_c('small',[_vm._v("- Ron Teixeria, client")])]),_c('div',{staticClass:"bubble"},[_vm._v("The tool Kim built to manage the Great Deals Stores has made my job much easier. I no longer have to manually process excel files, create test links to send out to other teams (they are now generated on the fly), and it automatically checks everything to make sure it will validate in our CMS. This has been a huge time saver for me! "),_c('small',[_vm._v("- Vijay Soppa, Web Development Team / GDS Manager")])]),_c('div',{staticClass:"bubble"},[_vm._v("I have closely worked with Kim for the last three years and have seen her deliver day in and day out. Kim has an affinity for solving business problems and pushing the boundaries on every project that she is assigned to. Kim was our Sr. Developer where she diligently managed and developed all of the modular and object-oriented frameworks utilized by the rest of the team. Kim's raw talent in development has allowed her to be the best developer of the team where she constantly perfected her craft. Simply put, Kim was my go-to-developer for many of the larger Toys\"R\" Us initiatives that needed to be developed in a time crunch. "),_c('br'),_c('br'),_vm._v("The 2016 Big Book Toy Finder, for example, came down to the wire and it was only possible because of Kim's development efforts. Her tenacious mindset towards the project ensured that it launched on time while wearing many hats from the backend tool that ingested the data, to the front-end code that powered the web application. This experience generated more than 5.2M which surpassed everyone's expectations. "),_c('br'),_c('br'),_vm._v(" There are many great things I can say about Kim and her impeccable work ethic. She is a hard worker, team player, team builder, knowledgeable and overall a well-rounded developer. I highly recommend Kim as she is a rockstar!!! "),_c('small',[_vm._v("- Jose Gonzalez, Sr. UX Designer")])])])}]

